import { Menu, Bell, Search, MessageSquare, LogOut, Trash2 } from 'lucide-react'
import { useAuth, forceCompleteLogout } from '../contexts/AuthContext'
import { logoutUser } from '../firebase/auth'
import './Header.css'

export default function Header({ toggleSidebar }) {
  const { userData } = useAuth()

  const handleLogout = async () => {
    if (confirm('     ')) {
      await logoutUser()
    }
  }

  const handleForceLogout = async () => {
    if (confirm('        ')) {
      await forceCompleteLogout()
    }
  }

  // Get user initials for avatar
  const getUserInitials = (name) => {
    if (!name) return ''
    return name.split(' ').map(n => n[0]).join('').toUpperCase().slice(0, 2)
  }

  // Get role display name
  const getRoleDisplay = (role) => {
    const roles = {
      admin: '',
      manager: '',
      employee: ''
    }
    return roles[role] || ''
  }

  return (
    <header className="header">
      <div className="header-right">
        <button className="menu-button" onClick={toggleSidebar}>
          <Menu size={24} />
        </button>

        <div className="search-container">
          <Search size={20} />
          <input
            type="text"
            placeholder="  ..."
            className="search-input"
          />
        </div>
      </div>

      <div className="header-left">
        <button className="icon-button" title="">
          <MessageSquare size={20} />
          <span className="notification-badge">3</span>
        </button>

        <button className="icon-button" title="">
          <Bell size={20} />
          <span className="notification-badge">5</span>
        </button>

        <div className="user-menu">
          <div className="user-avatar">
            {getUserInitials(userData?.displayName)}
          </div>
          <div className="user-info">
            <div className="user-name">{userData?.displayName || ''}</div>
            <div className="user-role">{getRoleDisplay(userData?.role)}</div>
          </div>
        </div>

        <button className="icon-button" onClick={handleForceLogout} title="   " style={{ color: '#dc3545' }}>
          <Trash2 size={20} />
        </button>

        <button className="icon-button logout-button" onClick={handleLogout} title=" ">
          <LogOut size={20} />
        </button>
      </div>
    </header>
  )
}
